ont-awesome-icon icon=\"bars\" />\n    </b-navbar-toggle>\n\n    <b-collapse is-nav id=\"header-tabs\">\n      <b-navbar-nav class=\"ml-auto\">\n        <b-nav-item to=\"/\" exact>\n          <span>\n            <font-awesome-icon icon=\"home\" />\n            <span v-text=\"$t('global.menu.home')\">Home</span>\n          </span>\n        </b-nav-item>\n        <b-nav-item-dropdown right id=\"entity-menu\" v-if=\"authenticated\" active-class=\"active\" class=\"pointer\" data-cy=\"entity\">\n          <span slot=\"button-content\" class=\"navbar-dropdown-menu\">\n            <font-awesome-icon icon=\"th-list\" />\n            <span class=\"no-bold\" v-text=\"$t('global.menu.entities.main')\">Entities</span>\n          </span>\n          <b-dropdown-item to=\"/device\">\n            <font-awesome-icon icon=\"asterisk\" />\n            <span v-text=\"$t('global.menu.entities.device')\">Device</span>\n          </b-dropdown-item>\n          <b-dropdown-item to=\"/device-model\">\n            <font-awesome-icon icon=\"asterisk\" />\n            <span v-text=\"$t('global.menu.entities.deviceModel')\">Device Model</span>\n          </b-dropdown-item>\n          <b-dropdown-item to=\"/customer-details\">\n            <font-awesome-icon icon=\"asterisk\" />\n            <span v-text=\"$t('global.menu.entities.customerDetails')\">Customer Details</span>\n          </b-dropdown-item>\n          <b-dropdown-item to=\"/shopping-cart\">\n            <font-awesome-icon icon=\"asterisk\" />\n            <span v-text=\"$t('global.menu.entities.shoppingCart')\">Shopping Cart</span>\n          </b-dropdown-item>\n          <b-dropdown-item to=\"/device-order\">\n            <font-awesome-icon icon=\"asterisk\" />\n            <span v-text=\"$t('global.menu.entities.deviceOrder')\">Device Order</span>\n          </b-dropdown-item>\n          <!-- jhipster-needle-add-entity-to-menu - JHipster will add entities to the menu here -->\n        </b-nav-item-dropdown>\n        <b-nav-item-dro